.form {
  display: flex;
  flex-wrap: wrap;
  background-color: transparent;
  color: $default-color;
  box-sizing: border-box;

  transition: 1s;

  @media (max-width: 753px) {
    flex-direction: column;
    align-items: flex-end;
  }

  @media (max-width: 575px) {
    align-items: center;
    width: 100%;
  }

  & label {
    margin-right: 33px;
    width: calc(100% - 33px - 148px);

    min-width: 420px;
    box-sizing: border-box;

    @media (max-width: 1580px) {
      min-width: 320px;
    }

    @media (max-width: $desktop) {
      min-width: 300px;
    }

    @media (max-width: $table) {
      min-width: 252px;
    }

    @media (max-width: 815px) {
      min-width: 250px;
    }

    @media (max-width: 753px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 22px;
    }
  }

  & input {
    padding: 10px 15px;
    width: 100%;
    height: 100%;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;
    background-color: $default-color;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    color: #a9a0b6;

    outline: none;

    @media (max-width: $mobile) {
      padding: 16px 15px;
    }

    &::placeholder {
      color: #a9a0b6;
      opacity: 1;
    }

    &:focus {
      opacity: 1;
    }
  }

  & input:valid {
    border-color: green;
  }

  & input:invalid {
    border-color: red;
  }

  & input:required:valid {
    border-color: green;
  }

  & button {
    padding: 10px;

    min-width: 148px;
    color: inherit;
    font-family: $mainFontMedium;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;

    background: #eeaa12;
    border-radius: 50px;
    border: none;
    box-sizing: border-box;
    cursor: pointer;

    outline: none;
    transition: 0.5s;

    &:focus,
    &:hover {
      background-color: #ffbe2d;
    }
  }
}
