.container {
  max-width: 1920px;
  width: 100%;
  padding: 0 300px;
  margin: 0 auto;

  box-sizing: border-box;

  @media (max-width: 1840px) {
    padding: 0 150px;
  }


  @media (max-width: 1640px) {
    padding: 0 100px;
  }

  @media (max-width: 1340px) {
    padding: 0 50px;
  }

  @media (max-width: $table) {
    padding: 0 32px;
  }

  @media (max-width: 768px) {
    padding: 0 24px;
  }

  @media (max-width: $mobile) {
    padding: 0 18px;
  }
}
